// hugo gen chromastyles --style=onedark > syntax.css
.dark {
  /* Background */
  .bg {
    color: #abb2bf;
    background-color: #282c34;
  }
  /* PreWrapper */
  .chroma {
    color: #abb2bf;
    background-color: #282c34;
  }
  /* Other */
  .chroma .x {
  }
  /* Error */
  .chroma .err {
  }
  /* CodeLine */
  .chroma .cl {
  }
  /* LineLink */
  .chroma .lnlinks {
    outline: none;
    text-decoration: none;
    color: inherit;
  }
  /* LineTableTD */
  .chroma .lntd {
    vertical-align: top;
    padding: 0;
    margin: 0;
    border: 0;
  }
  /* LineTable */
  .chroma .lntable {
    border-spacing: 0;
    padding: 0;
    margin: 0;
    border: 0;
  }
  /* LineHighlight */
  .chroma .hl {
    background-color: #3d4148;
  }
  /* LineNumbersTable */
  .chroma .lnt {
    white-space: pre;
    -webkit-user-select: none;
    user-select: none;
    margin-right: 0.4em;
    padding: 0 0.4em 0 0.4em;
    color: #55595f;
  }
  /* LineNumbers */
  .chroma .ln {
    white-space: pre;
    -webkit-user-select: none;
    user-select: none;
    margin-right: 0.4em;
    padding: 0 0.4em 0 0.4em;
    color: #55595f;
  }
  /* Line */
  .chroma .line {
    display: flex;
  }
  /* Keyword */
  .chroma .k {
    color: #c678dd;
  }
  /* KeywordConstant */
  .chroma .kc {
    color: #e5c07b;
  }
  /* KeywordDeclaration */
  .chroma .kd {
    color: #c678dd;
  }
  /* KeywordNamespace */
  .chroma .kn {
