.home {
  .home-profile {
    @include transform(translateY(16vh));
    padding: 0 0 0.5rem;
    text-align: center;

    .home-avatar {
      padding: 0.5rem;

      img {
        display: inline-block;
        width: 8rem;
        height: auto;
        margin: 0 auto;
        @include border-radius(100%);
        @include box-shadow(0 0 0 0.3618em rgba(0, 0, 0, 0.05));
        @include transition(all 0.4s ease);

        &:hover {
          position: relative;
          @include transform(translateY(-0.75rem));
        }
      }
    }

    .home-title {
      font-size: 1.3rem; /* 增大字体大小，使其更显眼 */
      font-weight: bold;
      margin: 0;
      padding: 1rem; /* 增加内边距 */
      color: #333; /* 默认文本颜色 */
      // text-shadow: 0 2px 4px rgba(0, 0, 0, 0.5); /* 添加文本阴影 */
      border-radius: 10px; /* 圆角边框 */
      transition: all 0.3s ease; /* 平滑过渡效果 */
      text-align: center; /* 文本居中 */
      letter-spacing: 2px; /* 字间距 */
      word-spacing: 4px; /* 单词间距 */
      max-width: 80%; /* 最大宽度 */
      margin: 2rem auto; /* 外边距自动 */
    }

    .home-subtitle {
      font-size: 1rem;
      font-weight: normal;
      margin: 0;
      padding: 0.5rem;
    }

    .links {
      padding: 0.5rem;
      font-size: 1.5rem;

      a * {
        vertical-align: text-bottom;
      }

      img {
        height: 1.5rem;
        padding: 0 0.25rem;
      }
    }

    .home-disclaimer {
      font-size: 1rem;
      line-height: 1.5rem;
      font-weight: normal;
      margin: 0;
      padding: 0.5rem;
      color: $global-font-secondary-color;
    }
  }
}

.home[posts] {
  .home-profile {
    @include transform(translateY(0));
    padding-top: 2rem;
  }

  .home-avatar img {
    width: 6rem;
  }

  .summary {
    padding-top: 1rem;
    padding-bottom: 0.8rem;
    color: $global-font-color;
    border-bottom: 3px dashed $global-border-color;

    .featured-image-preview {
      width: 100%;
      padding: 30% 0 0;
      position: relative;
      margin: 0.6rem auto;
      @include transition(transform 0.4s ease);

      img {
        border-radius: 15px;
        position: absolute;
        width: 100%;
        height: 100%;
        left: 0;
        top: 0;
        object-fit: cover;
        /* 增强样式 */
        box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1); /* 添加阴影效果 */
        transition: transform 0.3s ease, box-shadow 0.3s ease; /* 平滑过渡效果 */
        border: 1px solid #ddd; /* 添加边框 */
      }

      /* 鼠标悬停效果 */
      &:hover {
        border-radius: 15px;
        transform: scale(1.05); /* 鼠标悬停时放大 */
        box-shadow: 0 8px 16px rgba(0, 0, 0, 0.1); /* 鼠标悬停时增加阴影深度 */
      }
    }

    .single-title {
      font-size: 1.25rem;
      line-height: 140%;
      margin: 0.4rem 0;
    }

    .content {
      @include box(vertical);
      margin-top: 0.3rem;
      width: 100%;
      overflow: hidden;
      text-overflow: ellipsis;
      @include overflow-wrap(break-word);
      color: $global-font-secondary-color;

      h2,
      h3,
      h4,
      h5,
      h6,
      p {
        font-size: 1rem;
        line-height: 1.5;
        display: inline;

        &::after {
          content: "\A";
          white-space: pre;
        }
      }

      h2 {
        font-size: 1.125rem;
      }

      @include link(false, true);

      b,
      strong {
        color: $global-font-secondary-color;
      }
    }

    .post-footer {
      margin-top: 0.4rem;
      display: flex;
      justify-content: space-between;
      align-items: center;
      font-size: 0.875rem;

      @include link(false, false);

      .post-tags {
        padding: 0;

        @include link(true, true);
      }
    }
  }
}
